import { cssProps } from '@oracle/oraclejet-cssprops';
import { MessageAnimation, MessagesManager } from '../UNSAFE_Message';
import { Item } from '../utils/UNSAFE_dataProvider';
import { ComponentMessage, ComponentMessageItem } from './ComponentMessage';

const rootStyles = cssProps`${{
  overflowX: 'hidden',
  overflowY: 'hidden'
}}`;

/**
 * Converts the messages data into Item
 *
 * @param messages The messages data passed down as prop
 * @returns The messages data as Item
 */
function generateMessagesData(messages: ComponentMessageItem[]) {
  return messages.map<Item<number, ComponentMessageItem>>((message, index) => {
    return {
      key: index,
      data: {
        closeAffordance: 'off',
        severity: message.severity || 'error',
        detail: message.detail
      }
    };
  });
}

/**
 * Props for the ComponentMessaging component
 */
type Props = {
  /**
   * An object of various animation effects for the transitions.
   */
  animations?: Record<string, MessageAnimation[]>;

  /**
   * The label of the field which is showing this error
   */
  fieldLabel?: string;

  /**
   * Data for the messages. This data is used for rendering each message.
   */
  messages?: ComponentMessageItem[];
};

export function ComponentMessageContainer({ animations, fieldLabel, messages = [] }: Props) {
  return (
    <div class={rootStyles}>
      <MessagesManager animations={animations} data={generateMessagesData(messages)}>
        {({ item }) => (
          <ComponentMessage
            key={item.key}
            detail={item.data.detail}
            fieldLabel={fieldLabel}
            severity={item.data.severity}
          />
        )}
      </MessagesManager>
    </div>
  );
}
